import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FormsModule } from '@angular/forms';

import { UserService } from 'src/app/api/controllers/User';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedToasterService } from 'src/app/shared/services/shared-toaster.service';

import * as moment from 'moment';

@Component({
  selector: 'app-visitor-checkin',
  templateUrl: './visitor-checkin.component.html',
  styleUrls: ['./visitor-checkin.component.scss']
})
export class VisitorCheckinComponent implements OnInit {

  isVisitorSubmitted: boolean = true;
  isVisitorAdded:boolean = false;
  isError: boolean = false;
  errorMessage: string = "";

  visitor: any;

  visitByData: any;
  visitTypeId: string = "";

  apartmentUnitId: number;

  isExpectedVisitor: boolean = false;

  visitorStatus: string = "";
  minDate;
  isUnitVisitor = false;
  iscommunityVisitor = false;
  visitCategoryData;
  visitCategoryId;
  blockUnitData: any;
  actualInTime: any;
  actualOutTime: any;
  actualVisitorCount: any;
  actualDuration: any;
  expectedDuration: any;
  minOutDate: any;
  minExOutTime: any;
  isValidphoneNumber:boolean = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private userService: UserService,
    private apartmentService: ApartmentService,
    private visitorService: VisitorService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private sessionService: SessionService,
    private SharedToaster: SharedToasterService,
  ) {

    this.minDate = moment(new Date()).format('YYYY-MM-DD');
  }


  isCheckout() {
    return this.visitorStatus == 'checkout' ? true : false;
  }
  
  IsvalidatePhoneNo(val):boolean{
    var retVal = true;
      var rex =new RegExp(/^[+]?\d+$/);
      var isValid = rex.test(val);
      console.log(val.length);
      if(val.length>13){
        retVal = false;
        var errorDetails = {
          msg : "phone no length should be within 13 characters",
          type: "Error"
         };
         this.sharedService.setCustomAlertMessage(errorDetails);
      }
      if(!isValid){
        retVal = false;
        var errorDetails = {
          msg : "Kindly enter proper phoneno",
          type: "Error"
         };
         this.sharedService.setCustomAlertMessage(errorDetails);
      }
    return retVal;
    }

  submitAddVisitorForm(form) {

    let num = this.isCheckout() ? this.visitor.visitorPhone : this.visitor.expectedVisitorPhone;

    if(!this.IsvalidatePhoneNo(num))
   return true;
    
    this.isVisitorSubmitted = true;
    this.isError = false;

    if (!this.isCheckout()) {
      this.visitor.expectedDurationInHours = this.getDurationInHours(this.actualInTime, this.visitor.expectedVisitorOutTime);
        let visitorDetails = {
          "expectedVisitorId": parseInt(this.route.params['value'].id),
          "apartmentUnitId": this.apartmentUnitId,
          "apartmentId": this.sessionService.apartmentId,
          "visitorName": this.visitor.expectedVisitorName,
          "visitorCount": this.visitor.expectedVisitorCount,
          "visitorPhone": this.visitor.expectedVisitorPhone.toString(),
          "visitorVehicleTypeId": 1,
          "visitorVehicleNumber": "string",
          "visitingPlaceId": 1,
          "passNumber": this.visitor.passNumber,
          "isCheckedIn": true,
          "visitorInTime": this.actualInTime,
          "isCheckedOut": false,
          "visitorOutTime": this.visitor.expectedVisitorOutTime,
          "expectedDuration": this.visitor.expectedDurationInHours,
          "meetingPersonId": 1,
          "meetingPersonName": "string",
          "visitTypeId": parseInt(this.visitTypeId),
          "visitorCategoryId": parseInt(this.visitCategoryId),
          "visitorsOrg": "string",
          "entryGateId": null,
          "exitGateId": null,
          "purpose": this.visitor.purpose,
          "enteredBy": parseInt(this.sessionService.userId),
          "isActive": true,
          "insertedBy": parseInt(this.sessionService.userId),
          "insertedOn": new Date().toISOString(),
          "updatedBy": parseInt(this.sessionService.userId),
          "updatedOn": new Date().toISOString(),
          "expectedVisitorInTime": this.visitor.expectedVisitorInTime,
          "expectedVisitorOutTime": this.visitor.expectedVisitorOutTime,
          "expectedVisitorDuration": this.visitor.expectedDuration
    
      }
        let params = {
          visitor: visitorDetails
        }

      this.visitorService.addVisitor(params).subscribe((res: any) => {

        if (res.message) {
          this.isVisitorAdded = true;
          // this.sharedService.setAlertMessage("Visitor Checked In successfully");
          // this.router.navigate(['/ams/visitor/expected-visitor'])
        }
        else {

          this.isVisitorSubmitted = false;
          this.isVisitorAdded = false;
          this.isError = true;
          this.errorMessage = res.errorMessage;
        }

      },
        error => {

          this.isVisitorSubmitted = false;
          this.isVisitorAdded = false;
          this.isError = true;
          this.errorMessage = error;

        });

    }
    else {

      let visitorDetails = {
        "visitorId": parseInt(this.route.params['value'].id),
        "checkOutTime": '2020-05-19T07:10:33.863',
        "updatedBy": parseInt(this.sessionService.userId),
      }
      
      this.visitorService.checkOutVisitor(visitorDetails).subscribe((res: any) => {

        if (res.message) {
          this.isVisitorAdded = true;
          // this.sharedService.setAlertMessage("Visitor checked out successfully");
        }
        else {

          this.isVisitorSubmitted = false;
          this.isError = true;
          this.isVisitorAdded = false;
          this.errorMessage = res.errorMessage;
        }

      },
        error => {

          this.isVisitorSubmitted = false;
          this.isError = true;
          this.errorMessage = error;
          this.isVisitorAdded = false;

        });

    }

  }

  ngOnInit() {

    this.visitor = {};

    if (this.route.params['value'].id != undefined) {

      this.visitorStatus = this.route.params['value'].type;

      if (this.visitorStatus == 'checkout') {

        this.visitorService.getVisitorById(this.route.params['value'].id).subscribe((res: any) => {
        
          let visitorIdParam = {
            visitorId : this.route.params['value'].id
          }
  
          this.visitorService.getVisitorById(visitorIdParam).subscribe((res:any) => {
            this.visitor = res[0];

          this.visitTypeId = this.visitor.visitTypeId;
          this.visitCategoryId = this.visitor.visitorCategoryId;
          this.visitor.purpose = '';
          this.visitor.visitorOutTime = moment(new Date());

          let inDate = moment(this.visitor.visitorInTime);
          let outDate = moment(this.visitor.visitorOutTime);
          let diffDuration = moment.duration(outDate.diff(inDate));
          this.expectedDuration = diffDuration.days() + " days " + this.isSingleDigit(diffDuration.hours()) + ":" + this.isSingleDigit(diffDuration.minutes()) + " hours";
          this.isVisitorSubmitted = false;
          this.minOutDate = new Date();
        },
          error => {

          }
        );
        });
      }
      else if (this.visitorStatus == 'checkin') {
        let expectedVisitorIdParam = {
          expectedVisitorId : this.route.params['value'].id
        }
        this.visitorService.getExpectedVisitorById(expectedVisitorIdParam).subscribe((res: any) => {

          this.visitor = res[0];
          console.log('this.visitor', this.visitor)
          this.visitor.purpose = '';
          this.visitTypeId = this.visitor.visitTypeId;
          this.visitCategoryId = this.visitor.visitorCategoryId;
          this.actualInTime = moment(new Date());
          this.isVisitorSubmitted = false;
          this.minExOutTime = new Date(this.actualInTime);
          this.expectedDuration = this.getDuration(this.actualInTime, this.visitor.expectedVisitorOutTime);
          this.visitor.expectedDurationInHours = this.getDurationInHours(this.actualInTime, this.visitor.expectedVisitorOutTime);
        },
          error => {

          }
        );


      }

    }


    let visitParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 15
    }

    //visit type
    this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res: any) => {

      this.visitByData = res;

    },
      error => {

      }
    );
    let categoryParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 100
    }    
    //visit type
    this.lookupService.getLookupValueByLookupTypeId(categoryParams).subscribe((res: any) => {

      this.visitCategoryData = res.filter(item => {
        return item.isActive;
      });

    },
      error => {

      }
    );
    let apartmentParams = {
      userId: parseInt(this.sessionService.userId)
    }

    this.apartmentService.getApartmentBlockUnitByUserId(apartmentParams).subscribe((res: any) => {

      this.apartmentUnitId = res[0].apartmentBlockUnitId;

    },
      error => {

      }
    );
    this.visitor.expectedVisitorInTime = moment(new Date());
    this.visitor.visitorInTime = moment(new Date());

    this.apartmentService.getAllApartmentBlockUnits().subscribe((res: any) => {

      this.blockUnitData = res;

    });
  }

  navigateTo(param) {
    if(param){
    this.router.navigate(['/ams/visitor/checkout']);
    }else{
      this.router.navigate(['/ams/visitor/expected-visitor']);
    }
  }

  changeVisitorType(id) {
    console.log(id);
    let result = this.visitByData.filter(filterId => id == filterId.lookupValueId);
    console.log(result);
    this.isUnitVisitor = (result[0].lookupValueName == 'Unit Visitor') ? true : false;
    this.iscommunityVisitor = (result[0].lookupValueName == "Community Visitor") ? true : false;
  }

  expectedDurationChange(date1, date2) {
    if (this.minDate > moment(date1).format('YYYY-MM-DD')) {
      this.visitor.expectedVisitorInTime = undefined;
      this.SharedToaster.openSnackBar("Please Select valid Date", '');
      return;
    }

    if (this.minDate > moment(date2).format('YYYY-MM-DD')) {
      this.visitor.expectedVisitorOutTime = undefined;
      this.SharedToaster.openSnackBar("Please Select Out-time greater than In-time", '');
      return;
    }
   
    if (date2) {
      if (moment(date1).format('YYYY-MM-DD') > moment(date2).format('YYYY-MM-DD')) {
        this.visitor.expectedVisitorOutTime = undefined;
        this.SharedToaster.openSnackBar("Please Select Out-time greater than In-time", '');
        return;
      }
    }
    this.expectedDuration = this.getDuration(date1, date2);
    this.visitor.expectedDurationInHours = this.getDurationInHours(date1, date2);
  }
  getDuration(date1, date2) {
    let inDate = moment(date1);
    let outDate = moment(date2);
    let diffDuration = moment.duration(outDate.diff(inDate));
    return  diffDuration.days() + " days " + this.isSingleDigit(diffDuration.hours()) + ":" + this.isSingleDigit(diffDuration.minutes()) + " hours";
  }
  getDurationInHours(date1, date2){
    let inDate = moment(date1);
    let outDate = moment(date2);
    let sendDuration = moment.duration(outDate.diff(inDate));
    let hours = sendDuration.asHours();
    return Math.round(hours);
  }
  isSingleDigit(digit) {
    if (digit <= 0) {
      return '00';
    } else {
      return (digit.toString().length > 1) ? digit : '0' + digit;
    }

  }

}
