import { Component, OnInit, Injector, HostListener, ViewChild } from '@angular/core';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';

import * as moment from 'moment';
import { ConfirmDialogModel, CommonConfirmModalComponent } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';
import { MatDialog } from '@angular/material/dialog';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router } from '@angular/router';

@Component({
  selector: 'app-visitor-expected-visitor',
  templateUrl: './visitor-expected-visitor.component.html',
  styleUrls: ['./visitor-expected-visitor.component.scss']
})
export class VisitorExpectedVisitorComponent implements OnInit {

  visitorData:string = "";
  visitorListData:any;

  isVisitorDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  visitor:any;

  modalService:any;

  totalFilterItems:any;
  visitTypeData:any;
  visitCategoryData:any;
  selectedInput:string = "";
  columnField: any = {};
  result: string = '';
  listData: any;
  columnData:any;
  gridSourceData:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;


  constructor(
    private router: Router,
    private injector: Injector,
  	private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sessionService: SessionService,
    public dialog: MatDialog,
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  getDateTime(val){
    if(val != ''){
      return moment(val).format("MM/DD/YY hh:mm")
    }else{
      return ''
    }
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  getTimeFormat(dateTime){
    return moment(dateTime).format("YYYY-MM-DD HH:mm");
  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Id',
         datafield: 'serialNo',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
      }, {
          text: 'Visitor Name',
          datafield: 'expectedVisitorName',
          width: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },{
         text: 'Phone Number',
         datafield: 'expectedVisitorPhone',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Expected Date/Time of Visit',
         datafield: 'expectedVisitorInTime',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getDateTime(value) +'</div>'
        },
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Expected Time-out',
         datafield: 'expectedVisitorOutTime',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getDateTime(value) +'</div>'
        },
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Visit Type',
         datafield: 'visitType_Label',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Visitor category',
         datafield: 'visitorCategoryId_Label',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
          text: 'Tower Unit',
          datafield: 'block_Unit',
          cellsrenderer: cellsrenderer,
          minwidth: 170,
          renderer: columnrenderer 
    }, {
         text: 'Actions',
         cellsalign: 'center', 
         align: 'center',
         width: 120,
         cellsrenderer: (row: number): string => {
          return '<div class="simple-actions">'
          +'<a href="javascript:void(0)" onClick="navigateTo('+ row +')" ><img src="assets/images/checkin-icon.svg" width="20" /> </a>'
          +'</div>'
      },
         renderer: columnrenderer
     }];

  	
    let params = {
      apartmentId: this.sessionService.apartmentId
    }

  	this.visitorService.getExpectedVisitorsByApartmentId(params).subscribe((res:any) => {

      if(res.errorMessage){
        this.isVisitorDataLoaded = true;
    this.visitorListData = [];
     
  }else{
    this.visitorListData = res;

    console.log("visitorListData", this.visitorListData[0].visitTypeId);
       //filter active true items
  this.visitorListData = res.filter(data => {
    return data.isActive
  })
  
  this.gridSourceData = {
    localdata: this.visitorListData,
    datatype: "array"
  }

  this.listData = new jqx.dataAdapter(this.gridSourceData);
       
  this.totalItems = this.visitorListData.length;
  this.isVisitorDataLoaded = true;
  }
   
    let visitParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 15
    }
    
    //get visit type
    this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res:any) => {

      this.visitTypeData = res;
      this.checkVisitTypeData();

    },
    error => {
      
    }
    );
      
  },
  error => {
    
  }
  )
   
  }
  getPrintParams(event){
    this.datagrid.exportdata(event, 'expectedVisitorData');
  }

  checkVisitTypeData() {
    this.visitTypeData.forEach(element => {
      if(element.lookupValueId == this.visitorListData[0].visitTypeId){
        this.visitorListData[0].visitTypeName = element.lookupValueName.toLowerCase();
      }
    });
  }

  @HostListener('window:navigateTo', ['$event.detail'])
  navigateTo(detail) {

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let expectedVisitorId = dataRecord.expectedVisitorId
    this.router.navigateByUrl('/ams/visitor/edit-visitor/' + expectedVisitorId + '/checkin');

  }

  onGlSearchFilter() {

    if(this.visitorData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.visitorData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  //print button
  print(){

  const confirmationMessage = `Are you sure, you want to Print?`;
  const dialogData = new ConfirmDialogModel("Confirm Action", confirmationMessage);

  const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
    panelClass: 'material',
    disableClose: true,
    data: dialogData
  });

  dialogRef.afterClosed().subscribe(dialogResult => {
    this.result = dialogResult;
    if(this.result){
      //code
    }
  })

  }

}
function navigateTo(row){
  var event = new CustomEvent('navigateTo', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).navigateTo = navigateTo