import { Component, OnInit, Injector } from '@angular/core';

import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SessionService } from 'src/app/core/session/session.service';
import { MatDialog } from '@angular/material/dialog';
import { ConfirmDialogModel, CommonConfirmModalComponent } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';

@Component({
  selector: 'app-visitor-setup',
  templateUrl: './visitor-setup.component.html',
  styleUrls: ['./visitor-setup.component.scss']
})
export class VisitorSetupComponent implements OnInit {

  isDataLoaded: boolean = false;
  isCategoryTypeSubmitted: boolean = true;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;

  unitFieldType: string = "unitno";
  unitOrder: boolean = true;

  isMobile: boolean = false;

  categoryData: any;
  categoryDataList: any;

  isCategoryTypeNew: boolean = false;
  isCategoryTypeUpdate: boolean = false;

  categoryType: string = "";
  categoryUpdateId: number = 0;

  isError: boolean = false;
  alertMessage: boolean = false;

  modalService: any;

  result: string = '';

  constructor(
    private injector: Injector,
    private lookupService: LookupService,
    public dialog: MatDialog,
    private sharedService: SharedService,
    private sessionService: SessionService
  ) {
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event) {
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type) {
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if (this.unitFieldType == type) {
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  addNewCategoryType() {

    this.isCategoryTypeNew = true;
    this.isCategoryTypeUpdate = false;

    setTimeout(() => {

      var elem = document.querySelector('.vehicle-type-card');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);

  }

  updateCategoryType(item) {

    this.isCategoryTypeUpdate = true;
    this.isCategoryTypeNew = false;
    this.categoryType = item.lookupValueName;
    this.categoryUpdateId = item.lookupValueId;

    setTimeout(() => {

      var elem = document.querySelector('.vehicle-type-card');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);

  }

  deleteCategoryType(item) {
    const confirmationMessage = `Are you sure you want to delete?`;
    const dialogData = new ConfirmDialogModel("Confirm Action", confirmationMessage);

    const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
      panelClass: 'material',
      disableClose: true,
      data: dialogData
    });

    dialogRef.afterClosed().subscribe(dialogResult => {
      this.result = dialogResult;
      if (this.result) {
        var params = {
          lookupValueId: item.lookupValueId,
          updateUserId: parseInt(this.sessionService.userId)
        }

        this.lookupService.deleteLookupvalue(params).subscribe((res: any) => {

          if(res.message){
            var errorDetails = {
          msg: 'Visitor category deleted successfully',
          type: "Success"
        };
        this.sharedService.setCustomAlertMessage(errorDetails);
        this.isDataLoaded = false;
            let categoryParams = {
              ApartmentId: this.sessionService.apartmentId,
              LookupTypeId: 100
            }

            this.lookupService.getLookupValueByLookupTypeId(categoryParams).subscribe((res: any) => {
              this.isDataLoaded = true;
              this.categoryDataList = res.filter((item, i) => {
                item.number = i + 1;
                return item.isActive;
              });
              
              
              this.totalItems = this.categoryDataList.length;

              if (this.totalItems > this.itemLimit) {
                this.ItemEndIndex = this.itemLimit;
              }
              else {
                this.ItemEndIndex = this.totalItems;
              }
              this.isDataLoaded = true;
          });
        } 
        else{
            this.isDataLoaded = true;
          }
        });
    
      }
    });
    }

  removeCategoryTypeBox() {

    this.isCategoryTypeNew = false;
    this.isCategoryTypeUpdate = false;
    this.isError = false;

    setTimeout(() => {

      var elem = document.querySelector('.vehicle-type-table-card');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);

  }

  submitCategoryTypeForm(form) {

    const confirmationMessage = `Are you sure you want to save this settings?`;
    const dialogData = new ConfirmDialogModel("Confirm Action", confirmationMessage);

    const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
      panelClass: 'material',
      disableClose: true,
      data: dialogData
    });

    dialogRef.afterClosed().subscribe(dialogResult => {
      this.result = dialogResult;
      if (this.result) {
        //code
        this.isCategoryTypeSubmitted = false;
        if (this.isCategoryTypeNew) {

          let details = {

            "lookupTypeId": 100,
            "lookupValueName": this.categoryType,
            "description": 'Visitor Category',
            "isActive": true,
            "insertedBy": parseInt(this.sessionService.userId),
            "insertedOn": new Date().toISOString(),
            "updatedBy": null,
            "updatedOn": null

          }

          let params = {
            lookupvalue: details
          }

          this.lookupService.addLookupValue(params).subscribe((res: any) => {

            if (res.message) {
              
              this.sharedService.setAlertMessage("Visitor Category added successfully");
              this.isCategoryTypeNew = false;
              this.isCategoryTypeSubmitted = true;
              this.categoryType = '';
              this.isDataLoaded = false;
              let categoryParams = {
                ApartmentId: this.sessionService.apartmentId,
                LookupTypeId: 100
              }

              this.lookupService.getLookupValueByLookupTypeId(categoryParams).subscribe((res: any) => {
                this.isDataLoaded = true;
                this.categoryDataList = res.filter((item, i) => {
                  item.number = i + 1;
                  return item.isActive;
                });
                
                
                this.totalItems = this.categoryDataList.length;

                if (this.totalItems > this.itemLimit) {
                  this.ItemEndIndex = this.itemLimit;
                }
                else {
                  this.ItemEndIndex = this.totalItems;
                }
                this.isDataLoaded = true;
              });


            }
            else {
              this.isDataLoaded = true;
              this.isCategoryTypeSubmitted = true;
              this.isError = true;
              this.alertMessage = res.errorMessage;
              var errorDetails = {
                msg: this.alertMessage,
                type: "Error"
              };
              this.sharedService.setCustomAlertMessage(errorDetails);
            }

          });

        }

        else {

          let details = {

            "lookupValueId": this.categoryUpdateId,
            "ApartmentId": this.sessionService.apartmentId,
            "lookupTypeId": 100,
            "lookupValueName": this.categoryType,
            "description": this.categoryType,
            "isActive": true,
            "updatedBy": parseInt(this.sessionService.userId),
            "updatedOn": new Date().toISOString()

          }

          let params = {
            lookupvalue: details
          }

          this.lookupService.updateLookupValue(params).subscribe((res: any) => {

            if (res.message) {

              let categoryParams = {
                ApartmentId: this.sessionService.apartmentId,
                LookupTypeId: 100
              }

              this.lookupService.getLookupValueByLookupTypeId(categoryParams).subscribe((res: any) => {
                this.isCategoryTypeUpdate = false;
                this.isCategoryTypeSubmitted = true;
                this.sharedService.setAlertMessage("Visitor Category updated successfully");

                this.categoryDataList = res.filter((item, i) => {
                  item.number = i + 1;
                  return item.isActive;
                });

                this.totalItems = this.categoryDataList.length;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

              });
            }
            else {
              this.isCategoryTypeSubmitted = true;
              this.isError = true;
              this.alertMessage = res.errorMessage;
              var errorDetails = {
                msg: this.alertMessage,
                type: "Error"
              };
              this.sharedService.setCustomAlertMessage(errorDetails);
            }

          });

        }

      }
      else{
        this.isCategoryTypeSubmitted = true;
      }
    })





  }

  ngOnInit() {

    let categoryListParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 100
    }

    this.lookupService.getLookupValueByLookupTypeId(categoryListParams).subscribe((res: any) => {

      this.categoryDataList = res.filter((item, i) => {
        item.number = i + 1;
        return item.isActive;
      });

      this.totalItems = this.categoryDataList.length;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isDataLoaded = true;
    });

  }

}

